@use '../../0-settings/const';
@use 'settings/global';

@mixin outer-container() {
  max-width: global.$grid__max-width;
  margin: auto;
  position: relative;
  box-sizing: border-box;
}



/**
 * container mixins
 */

@mixin grid-visual($gutter: global.$grid__gutter, $color: papayawhip) {
  $gradient: (
    to right,
    transparent,
    transparent $gutter,
    $color $gutter,
    $color percentage(1/const.$grid__columns)
  );

  background-position-x: $gutter / -2;
  background-image: repeating-linear-gradient($gradient);
}

@mixin grid-gutter($gutter: global.$grid__gutter) {
  // margin-/padding-right is set to 0 to avoid horizontal scrollbars
  // @see https://www.rawkblog.com/2019/01/flexbox-gutters-negative-margins-solved/

  /* critical:start */
  margin: $gutter/-2 0 $gutter/-2 $gutter*-1;
  /* critical:end */

  > * {
    /* critical:start */
    padding: $gutter/2 0 $gutter/2 $gutter;
    /* critical:end */
  }
}

@mixin grid-container($gutter: global.$grid__gutter) {
  /* critical:start */
  @include grid-gutter($gutter);

  display: flex;
  flex-wrap: wrap;
  min-width: 100%;
  box-sizing: border-box;
  /* critical:end */

  > * {
    /* critical:start */
    position: relative;
    // Prevent columns from becoming too narrow when at smaller grid tiers by
    // always setting `width: 100%;`. This works because we use `flex` values
    // later on to override this initial width.
    width: 100%;
    background-clip: content-box;
    /* critical:end */
  }
}

@mixin grid-cols-per-row($cols) {
  > * {
    @include grid-cell(percentage(1 / $cols));
  }
}



/**
 * column/cell mixins
 */

@mixin grid-columns($cols) {
  @include grid-cell(percentage($cols / const.$grid__columns));
}

@mixin grid-cell($width) {
  /* critical:start */
  flex: 0 0 $width;
  max-width: $width;
  /* critical:end */
}

@mixin grid-push($cols) {
  margin-left: percentage($cols / const.$grid__columns);
}

/// @see http://www.heydonworks.com/article/the-flexbox-holy-albatross
/// @param $threshold
/// @param $max-width width of element when the parent's width is SMALLER than threshold
/// @param $min-width width of element when the parent's width is BIGGER than threshold

@mixin grid-break-at($threshold, $max-width: 100%, $min-width: false) {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: calc((#{$threshold} - 100%) * 999);

  @if $min-width {
    @if unitless($min-width) {
      $min-width: percentage($min-width / const.$grid__columns);
    }

    min-width: $min-width;
  }

  @if $max-width {
    @if unitless($max-width) {
      $max-width: percentage($max-width / const.$grid__columns);
    }

    @if $min-width and unit($max-width) == unit($min-width) and $max-width < $min-width {
      @error 'max-width has to be greater than min-width';
    }

    @if $max-width != 100% {
      // max-width: 100% is the default (caused by flex-grow: 1)
      max-width: $max-width;
    }
  }
}
